async function getUserInfo() {
  try {
    const response = await axios({
      method: "post",
      url: 'http://localhost:4500/v2/user/login',
      data: {
        email: "test@test.com",
        password: "Password123!"
      },
      withCredentials: true
    });

    console.log(response);

    const userInfo: {
      email: string,
      name: string
    } = {
      email: response.data.data.email,
      name: response.data.data.name
    }

    console.log("userInfo",userInfo);

    return userInfo;
  } catch(e) {
    console.log(e);
    return null
  }
  
}

function* getPostsSaga() {
  try {
    const data = yield call(getUserInfo);
    console.log("data", data);
    yield put(signInUser({
      email: data.email,
      name: data.name
    }));
  } catch(e) {
    console.log(e);
  }
}

export function* postsSaga() {
  yield takeEvery(SIGNIN_ASYNC, getPostsSaga);
}

export const signInAsync = () => {
  return {type: SIGNIN_ASYNC}
}

type UserInfo = {
  email: string;
  name: string;
}

export function signInUser(userInfo: UserInfo) {
  return {
    type: SIGNIN,
    payload: {
      email: userInfo.email,
      name: userInfo.name
    }
  }
}

export function signInUserAsync() {
  return {
    type: SIGNIN_ASYNC
  }
}

export type AuthActions = ReturnType<typeof signInUser>
  | ReturnType<typeof signInUserAsync>;

const initialState: AuthState = (() => {
  return {
    email: "test@naver.com",
    name: "test"
  }
})();

export default function auth(state: AuthState = initialState, action: AuthActions) {
  switch (action.type) {
    case SIGNIN:
      console.log(action);
      return Object.assign({}, state, {
        email: action.payload.email,
        name: action.payload.name
      });
  
    default:
      return state;
  }
}